.header-body{
    width: 100%;
    height: 90px;
    position:fixed;
    z-index: 200;
    background: #226666;
    opacity: .99;
}

.header-nav{
    height: 100%;
    width: 90%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin: 0 auto;
    font-size: 80%;
}

.header-nav > button{
    width: 70%;
    height: 50%;
    background: #226666;
    border: 0px solid black;
}

@media(min-width: 320px){
    .header-nav{
        width: 90%
    }
}

@media(min-width: 620px){
    .header-nav{
        width: 80%
    }
}

@media(min-width: 820px){
    .header-nav{
        width: 70%
    }
}

@media(min-width: 1020px){
    .header-nav{
        width: 60%
    }
}

@media(min-width: 1220px){
    .header-nav{
        width: 50%
    }
}


