.main-body{
    width: 100%;
    height: 500px;
    padding: 0;
}
.main-pic{
    position: relative;
    width: 100%;
    height: 100%;
    opacity: .08;
}
.main-content{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    position: relative;
    bottom: 150px;
}

.main-content > h1 {
    color: #499273;
    font-size: 150%;
}

@media(min-width: 320px){
    .main-content > h1 {
    font-size: 200%;
    }
}

@media(min-width: 620px){
    .main-content > h1 {
        font-size: 250%;
    }
}

@media(min-width: 820px){
    .main-body{
        height: 700px;
    }
    .main-content > h1 {
        font-size: 300%;
    }
}


